import Cartcontrol from '@/components/cart-control/cart-control';
import BScroll from 'better-scroll';

export default {
  props:{
    selectFoods:{
      type:Array,
      default: ()=> []
    },
    deliveryPrice:{
      type:Number,
      default:0
    },
    minPrice:{
      type:Number,
      default:0
    }
  },
  data(){
    return {
      fold: true,
      balls:[
        {
          show:false
        },
        {
          show:false
        },
        {
          show:false
        },
        {
          show:false
        },
        {
          show:false
        }
      ],
      dropBalls: []
    }
  },
  components: {
    Cartcontrol
  },
  computed: {
    totalPrice() {
      let total = 0;
      this.selectFoods.forEach((item)=>{
        total += item.price * item.count;
      });
      return total;
    },
    totalCount() {
      let count = 0;
      this.selectFoods.forEach((item)=>{
          count += item.count;
      });
      return count;
    },
    payDesc() {
      if(this.totalPrice === 0){
        return `¥${this.minPrice}元起送`; 
      }else if(this.totalPrice<this.minPrice){
        let diff = this.minPrice - this.totalPrice;
        return `还差¥${diff}元起送`
      }else{
        return '去结算';
      }
    },
    payClass() {
      if(this.totalPrice< this.minPrice){
        return 'not-enough';
      }else{
        return 'enough';
      }
    },
    listShow() {
      if(!this.totalCount) {
        this.fold = true;
      }
      let show = !this.fold;
      if (show) {
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BScroll(this.$refs.listContent, {
              click: true
            });
          } else {
            this.scroll.refresh();
          }
        });
      }
      return show;
    }
  },
  methods: {
    toggleList(){
      if(!this.totalCount){
        return;
      }
      this.fold = !this.fold;
    },
    empty() {
      this.selectFoods.forEach((food) => {
        food.count = 0;
      });
    },
    drop(el) {
      this.balls.some((ball) => {
        if(!ball.show){
          ball.show = true;
          ball.el = el;
          this.dropBalls.push(ball);
          return true;
        }
      });
    },
    beforeDrop(el) {//过渡开始之前，小球需要先移动到起始位置，所以y值为负
      let count = this.balls.length;
      while (count--) {
        let ball = this.balls[count];
        if (ball.show) {
          let dpi = window.devicePixelRatio;
          let screenWidth = window.screen.width;
          let pixel = dpi*screenWidth;
          let rect = ball.el.getBoundingClientRect();
          let left = (82*(pixel/750))/dpi;
          let bottom = (58*(pixel/750))/dpi;
          let x = rect.left + (rect.width/2) -left;
          let y = -(window.innerHeight - rect.top - (rect.height/2) - bottom);
          el.style.display = 'block';
          el.style.webkitTransform = `translate3d(0,${y}px,0)`;
          el.style.transform = `translate3d(0,${y}px,0)`;
          let inner = el.getElementsByClassName('inner-hook')[0];
          inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
          inner.style.transform = `translate3d(${x}px,0,0)`;
        }
      }
    },
    dropping(el, done) {//小球移动完成后回到原位
      let rf = el.offsetHeight;
      this.$nextTick(() => {
        el.style.webkitTransform = 'translate3d(0,0,0)';
        el.style.transform = 'translate3d(0,0,0)';
        let inner = el.getElementsByClassName('inner-hook')[0];
        inner.style.webkitTransform = 'translate3d(0,0,0)';
        inner.style.transform = 'translate3d(0,0,0)';
        el.addEventListener('transitionend', done);
      });
    },
    afterDrop(el) {
      let ball = this.dropBalls.shift();
      if (ball) {
        ball.show = false;
        el.style.display = 'none';
      }
    }
  }
}



