const POSITIVE = 0;
const NEGATIVE = 1;
const ALL = 2;

export default {
  props: {
    ratings: {
      type: Array,
      default: ()=> []
    },
    selectType: {
      type: Number,
      default: ALL
    },
    onlyContent: {
      type: Boolean,
      default:()=> false
    },
    desc: {
      type: Object,
      default() {
        return {
          all:'全部',
          positive:'满意',
          negative: '不满意'
        }
      }
    }
  },
  computed: {
    positives() {
      return this.ratings.filter((rating) => {
        return rating.rateType === POSITIVE;
      });
    },
    negatives() {
      return this.ratings.filter((rating) => {
        return rating.rateType === NEGATIVE;
      });
    }
  },
  methods: {
    selected(type) {
      this.$emit('changeSelectType',type)
    },
    toggleContent() {
      this.$emit('changeContent',!this.onlyContent)
    }
  }
}



