export default {
  props: {
    food: {
      type: Object,
      default:()=> {}
    }
  },
  methods: {
    addCart(event) {
      if(!this.food.count){
        this.$set(this.food,'count',1);
      }else{
        this.food.count++;
      }
      this.$emit('add',event.target);
    },
    decreaseCart(event) {
      if(this.food.count){
        this.food.count--;
      }
    }
  }
};



