
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import Star from '@/components/star/star';
import Cls from '@/common/cls-name/cls-name';

export default {
props: {
seller: {
type: Object,
default:() => {}
}
},
data(){
return {
cls: [],
detailShow: false,
tabIndex: 0
}
},
components: {
Tab,
TabItem,
Star
},
created(){
this.cls = Cls;
this.tabIndex = this.$route.meta.selected;
},
methods: {
skipPage(index) {
this.tabIndex = index;
if (index == 0) {
this.$router.push({
name: "goods"
});
} else if (index == 1) {
this.$router.push({
name: "ratings"
});
} else if (index == 2) {
this.$router.push({
name: "seller"
});
}
},
showDetail() {
this.detailShow = true;
},
closeDetail() {
this.detailShow = false;
}
}
};



