import Header from "@/components/header/header";
import { getSeller } from '@/server/api';

export default {
  name: 'App',
  components:{
    Header
  },
  data() {
    return {
      seller: {}
    }
  },
  created(){
    getSeller().then(({data}) =>{
      this.seller = data.data.seller;
    })
  }
}



