import { getGoods } from '@/server/api';
import Cls from '@/common/cls-name/cls-name';
import BScroll from 'better-scroll';
import Shopcar from '@/components/shop-car/shop-car';
import Cartcontrol from '@/components/cart-control/cart-control';
import DetailFood from '@/view/food/food';

export default {
  props: {
    seller: {
      type: Object,
      default: ()=> {}
    }
  },
  data() {
    return {
      goods: [],
      cls: [],
      listHeight: [],
      scrollY: 0,
      currectFood: {}
    }
  },
  components: {
    Shopcar,
    Cartcontrol,
    DetailFood
  },
  created() {
    getGoods().then(({data}) =>{
        this.goods = data.data.goods;
        this.$nextTick(() =>{
          this.initScroll();
          this.calulateHeight();
        })
    }),
    this.cls = Cls;
  },
  computed: {
    currentIndex() {
      for(let i =0;i<this.listHeight.length;i++){
        let curr = this.listHeight[i];
        let next = this.listHeight[i+1];
        if(!next || (this.scrollY >= curr && this.scrollY < next)){
          return i;
        }
      }
      return 0;
    },
    sellectFoods() {
      let foods = [];
      this.goods.forEach((good) => {
        good.foods.forEach((food) => {
          if(food.count) {
            foods.push(food)
          }
        })
      })
      return foods;
    }
  },
  methods:{
    initScroll() {
      this.menuScroll = new BScroll(this.$refs.menuWrapper,{
        click:true
      })
      this.foodsScroll = new BScroll(this.$refs.foodsWrapper,{
        probeType: 3,
        click:true
      })
      this.foodsScroll.on('scroll',((position) => {
        this.scrollY = Math.abs(Math.round(position.y));
      }))
    },
    calulateHeight() {
      let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
      let height = 0;
      this.listHeight.push(height)
      Array.from(foodList).forEach(item => {
        height += item.clientHeight;
        this.listHeight.push(height)
      })
    },
    selectMenu(index) {
      let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
      let currentEle = foodList[index];
      this.foodsScroll.scrollToElement(currentEle,300);
    },
    drop(el) {
      //强行异步处理下落动画，优化效果
      this.$nextTick(() => {
        this.$refs.shopCar.drop(el);
      });
    },
    addBall(el) {                     
      this.drop(el);
    },
    selectFood(food) {
      this.currectFood = food;
      this.$refs.detailFood.show();
    }
  }
}



